.form-control {
    padding: 10px 20px;
    border-radius: 15px;
    background-color: rgb(62, 56, 75);
    display: flex;
    align-items: center;
}

.form-control input[type="text"] {
    flex: 1;
    height: 100%;
    width: 100%;
    margin-left: 10px;
    padding: 10px;
    background-color: inherit;
    color: white;
    border: none;
    outline: none;
    font-size: 15px;
    font-weight: 500;
}

.form-control__searchIcon {
    background-color: inherit;
    color: white;
    height: 23px !important;
    width: 23px !important;
}