@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

.app {
  min-height: 100vh;
  /* overflow: hidden; */
  background-color: rgb(39, 33, 60);
}

.dashboard {
    box-sizing: border-box;
}

.dashboard__left {
    min-height: 100vh;
    box-sizing: border-box;
    flex: 0.55 1;
    display: grid;
    justify-content: center;
    align-content: center;
    margin: auto;
    box-sizing: border-box;
    padding: 30px;
    align-items: center;
    color: white;
}

.dashboard__left-title > h3 {
    font-size: 30px;
    font-weight: 300;
    padding-bottom: 10px;
}

.dashboard__left-title > h3 > span {
    font-size: 25px;
    font-weight: 700;
}

.dashboard__left-title > p {
    font-size: 14px;
    font-weight: 100;
    padding-bottom: 30px;
}

.dashboard__left-users > h3 {
    font-weight: 500;
    margin-top: 50px;
    margin-bottom: 30px;
    font-size: 14px;
}

.search__entry-warning {
    font-size: 8px;
    margin-bottom: 10px;
    font-style: italic;
    color: rgb(118, 243, 111);
}

.search__entry-warning:nth-of-type(2) {
    color: red
}

.dashboard__right {
    border: 1px solid white;
    padding: 35px 70px;
    /* height: 100vh; */
    /* overflow-y: scroll; */
    background-color: whitesmoke;
    flex: 0.45 1;
    border-radius: 15px;
    margin: 5px 0;
    margin-right: 10px;
    box-sizing: border-box;
    position: relative;
}

.dashboard__right h3 {
    margin-bottom: 10px;
    font-weight: 800;
    text-transform: capitalize;
}

.dashboard__right p {
    margin-bottom: 10px;
    font-size: 12px;
}

.form-control-2 {
    padding: 7px 20px;
    border-radius: 25px;
    background-color: #ddd;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.form-control-2 input[type="text"] {
    flex: 1 1;
    height: 100%;
    width: 100%;
    margin-left: 10px;
    padding: 5px;
    color: #444;
    background-color: inherit;
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
}

.form-control-2__searchIcon {
    background-color: inherit;
    color: #555;
    height: 15px !important;
    width: 15px !important;
}

.dashboard__right-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 35px;
    border: 1px solid #ccc;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    background-color: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 60px;
}

.list__download-btn {
    text-decoration: none;
    padding: 7px 15px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    font-size: 11px;
    color: white;
    border: none;
    outline: none;
    background-color:  rgb(89, 40, 148);
    align-self: flex-start;
    cursor: pointer;
}

.download-icon {
    padding-right: 10px;
    height: 23px !important;
    width: 23px !important;
    transition: 2000ms;
}

.list__download-btn:hover {
    background-color:  rgb(65, 28, 109);
    box-shadow: 0 0 10px #333;
}

.list__download-btn:hover .download-icon {
    transform: rotate(-30deg);
}

@media screen and (min-width: 900px) {
    .dashboard {
        display: flex;
        justify-content: space-around;
    }
}
.users__buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.users__btn-container {
    display: flex;
    flex-direction: column;
    margin-right: 30px;
    align-items: center;
}

.users__btn-container p {
    font-weight: 300;
    font-size: 10px;
    text-transform: capitalize;
    margin-bottom: 15px;
}

.users__btn {
    padding: 25px;
    border-radius: 20px;
    border: none;
    outline: none;
    color: white;
    background-color: rgb(202, 34, 146);
    margin-bottom: 15px;
    transition: 700ms;
}

.users__btn:focus {
    transform: scale(1.2);
}

.users__btn-container:nth-child(2) .users__btn {
    background-color: rgb(33, 187, 172);
}

.users__btn-container:nth-child(3) .users__btn {
    background-color: rgb(89, 40, 148);
}

.icon {
    height: 30px !important;
    width: 30px !important;
}
.user__profile {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: space-between;
    background-color: rgb(252, 252, 252);
    box-shadow: 5px 10px 20px #ddd;
    padding: 20px;
    border-radius: 10px;
    margin: 15px 0;
    box-sizing: border-box;
    flex-wrap: wrap;
    position: relative;
    /* height: 300px; */
}

.user__profile-details {
    display: flex;
    align-items: center;
    flex: 1 1;
    flex-wrap: wrap;
}

.picture-avatar {
    height: 65px !important;
    width: 65px !important;
    border: 5px solid rgb(55, 211, 195);
    margin-right: 25px;
}

.user__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 15px;
    flex: 1 1;
    flex-wrap: wrap;
}

.user__info > h4 {
    font-size: 14px;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.user__info > h5 {
    font-size: 11px;
    margin-bottom: 10px;
    font-style: italic;
    color: #333;
    font-weight: 500;
    display: flex;
    flex-wrap: wrap;
}

.user__contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
}

.user__contact p {
    font-size: 10px;
    color: #777;
}

.user__btn {
    align-self: flex-end;
    border-radius: 8px;
    padding: 7px 12px;
    background-color: rgb(33, 187, 172);
    box-shadow: 5px 10px 20px rgb(166, 218, 213);;
    color: white;
    align-items: center;
}

.user__btn-icon {
    height: 15px !important;
    width: 15px !important;
}

.user__btn:hover {
    background-color: rgb(27, 124, 115);
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 350px;
    max-width: 400px;
    background-color: whitesmoke;
    z-index: 1000;
    border-radius: 20px;
    padding: 20px;
    transition: 1000ms;
}

.back-to-results-btn {
    border: none;
    background-color: transparent;
    display: flex;
    align-items: center;
}

.back-arrow-icon {
    height: 35px !important;
    width: 35px !important;
    color: rgb(55, 211, 195);;
}

.more__user-details {
    margin-top: 20px;
    display: flex;
    box-sizing: border-box;
}

.user__picture {
    height: 150px !important;
    width: 150px !important;
    border: 5px solid rgb(55, 211, 195);
    margin-right: 15px;
}

.user__name {
    margin-bottom: 15px;
    font-size: 16px;
}

.user__name > span {
    font-weight: 300;
}

.user__address {
    font-size: 13px;
    margin-bottom: 10px;
}

.user__email {
    background-color: #ddd;
    flex-wrap: wrap;
    font-size: 10px;
    
}

.user__registered {
    background-color: rgb(242, 194, 201);
    font-weight: 700;
}

.user__email,
.user__registered {
    padding: 8px 15px;
    border-radius: 10px;
    font-size: 10px;
    margin: 15px 0;
}

.user__phone,
.user__cell {
    font-size: 10px;
    margin: 20px 0;
}

.users__list {
    min-height: 60vh;
    margin-bottom: 65px;
}
.form-control {
    padding: 10px 20px;
    border-radius: 15px;
    background-color: rgb(62, 56, 75);
    display: flex;
    align-items: center;
}

.form-control input[type="text"] {
    flex: 1 1;
    height: 100%;
    width: 100%;
    margin-left: 10px;
    padding: 10px;
    background-color: inherit;
    color: white;
    border: none;
    outline: none;
    font-size: 15px;
    font-weight: 500;
}

.form-control__searchIcon {
    background-color: inherit;
    color: white;
    height: 23px !important;
    width: 23px !important;
}
.list__pagination-btns {
    display: flex;
    flex-wrap: wrap;
}

.pagination__btn {
    background-color: #ccc;
    font-size: 10px;
    font-weight: 700;
    padding: 7px 9px;
    border-radius: 5px;
    border: none;
    outline: none;
    margin: 2px;
    cursor: pointer;
}

.pagination__btn:hover,
.pagination__btn:focus {
    background-color: #111;
    color: white;
    transform: scale(1.1);
    box-shadow: 0 0 10px #333;
}
